<template>
<div> <el-breadcrumb style="padding-bottom:15px " separator="/">
  <el-breadcrumb-item :to="{ path: '/main' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>系统管理</el-breadcrumb-item>
  <el-breadcrumb-item>情绪小妙招管理</el-breadcrumb-item>
</el-breadcrumb>
  <el-card>
    <!--    搜索头部-->
    <el-row :gutter="40" style="padding-bottom: 15px">
      <el-col :span="8">
        <el-input placeholder="请输入内容" v-model="input3" class="input-with-select" @keyup.enter.native="Search"
                  clearable>
          <el-button slot="append" icon="el-icon-search" @click="Search"></el-button>
        </el-input>
      </el-col>
        <el-col :span="4">
          <el-button type="primary" size="mini" @click="addEmotion">添加心情小妙招</el-button>
        </el-col>
        <el-col :span="4" style="  position:absolute; left:90%;">
          <el-button type="primary" icon="el-icon-refresh-right" @click="getEmotionList"></el-button>
        </el-col>
    </el-row>
    <el-table
        :data="EmotionList.data"
        border
        style="width: 100%">
      <el-table-column
          prop="title"
          label="标题"
          width="400">
      </el-table-column>
      <el-table-column
          prop="concent"
          label="内容"
          width="400">
      </el-table-column>
      <el-table-column
          prop="id"
          label="操作"
          width="436">
        <div slot-scope="a">
          <el-button type="primary" size="mini" @click="updateEmotion(a.row)">查看/编辑</el-button>
          <el-button type="danger" size="mini" @click="deleteEmotion(a.row)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
  </el-card>
  <!--      分页控件-->
  <el-pagination
      @size-change="SizeChange"
      @current-change="CurrentChange"
      :current-page="page"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="limit"
      layout="total, sizes, prev, pager, next, jumper"
      :total="EmotionList.count">
  </el-pagination>
  <el-dialog
      title="编辑情绪小妙招"
      :visible.sync="dialogUpdateEmotion"
      width="30%">
    <!--        内容区域-->
    <el-form :model="UpdateEmotionList"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题" prop="title">
        <el-input  :rows="2" v-model="UpdateEmotionList.title" ></el-input>
      </el-form-item>
      <el-form-item label="内容" prop="contion">
        <el-input  type="textarea" :rows="2" v-model="UpdateEmotionList.concent" ></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogUpdateEmotion = false">取 消</el-button>
    <el-button type="primary" @click="updateEmotion1">保 存</el-button>
  </span>
  </el-dialog>
  <el-dialog
      title="添加树洞小纸条"
      :visible.sync="dialogAddEmotion"
      width="30%">
    <!--        内容区域-->
    <el-form :model="AddEmotionList"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题" prop="title">
        <el-input  :rows="2" v-model="AddEmotionList.title" ></el-input>
      </el-form-item>
      <el-form-item label="内容" prop="concent">
        <el-input  type="textarea" :rows="2" v-model="AddEmotionList.concent" ></el-input>
      </el-form-item>
      <el-button type="primary" @click="addEmotion1" class="a">添 加</el-button>
    </el-form>

  </el-dialog>
</div>
</template>

<script>
export default {
  name: "Emotion",
  data(){
    return{
      input3:'',
      page: 1,
      limit: 10,
      EmotionList:[],
      UpdateEmotionList:{
        id:'',
        title:'',
        concent:''
      },
      AddEmotionList:{
        title: "",
        concent: ""
      },
      dialogAddEmotion: false,
      dialogUpdateEmotion: false,
    }
  },
  methods:{
    //添加
    addEmotion(){
      this.dialogAddEmotion = !this.dialogAddEmotion
    },
    addEmotion1(){
      this.dialogAddEmotion = !this.dialogAddEmotion,
          this.$http.get("/EmotionController/addEmotion", {
            params: {
              id: this.AddEmotionList.id,
              title: this.AddEmotionList.title,
              concent: this.AddEmotionList.concent
            }
          }).then((res) => {
            console.log(res)
            this.EmotionList = res.data;
            if (res.data.code === 1) {
              this.$message.success("添加成功")
              this.getEmotionList();
            } else this.$message.error("添加失败")
          })
    },
    //关键字搜索
    Search() {
      console.log(this.input3)
      this.$http.get("/EmotionController/selectWhereEmotion", {
        params: {
          page: 1,
          limit: 10,
          Keyword: this.input3,
        }
      }).then((res) => {
        console.log(res.data)
        this.EmotionList = res.data;
        if (res.data.code === 1) {
          this.$message.success("搜索成功")
        } else this.$message.error("搜索失败")
      })
    },
    //请求查看用户列表数据
    getEmotionList() {
      this.$http.get("/EmotionController/selectEmotion", {
        params: {
          page: this.page,
          limit: this.limit,
        }
      }).then((res) => {
        // console.log(res)
        this.EmotionList = res.data;
        if (res.data.code === 1) {
          // this.$message.success("请求成功")
        } else this.$message.error("请求失败")
      })
    },
    //编辑按钮事件
    updateEmotion(row) {
      this.dialogUpdateEmotion = !this.dialogUpdateEmotion
      this.UpdateEmotionList = row;
      console.log(this.UpdateEmotionList)
    },
    //确定编辑事件
    updateEmotion1() {
      this.dialogUpdateEmotion = !this.dialogUpdateEmotion,
          this.$http.get("/EmotionController/updateEmotion", {
            params: {
              id: this.UpdateEmotionList.id,
              title: this.UpdateEmotionList.title,
              concent: this.UpdateEmotionList.concent
            }
          }).then((res) => {
            console.log(res)
            if (res.data.code === 1) {
              this.$message.success("保存成功")
              this.getEmotionList();
            } else this.$message.error("保存失败")
          })
    },
//删除按钮事件
    deleteEmotion(row) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.get("/EmotionController/deleteEmotion", {
          params: {
            id: row.id,
          }
        }).then((res) => {
          console.log(res)
          if (res.data.code === 1) {
            this.$message.success("删除成功")
            this.getEmotionList();
          } else this.$message.error("删除失败")
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //每页条数发生改变时触发
    SizeChange(limit) {
      this.limit = limit;
      this.getEmotionList();
    },
    //页码发生改变时触发
    CurrentChange(page) {
      this.page = page;
      this.getEmotionList();
    },
  },
  //  钩子函数
  created() {
    this.getEmotionList();
  },
}
</script>

<style scoped>
.a{
  margin-left: 360px;
  margin-top: 20px;
}

</style>